@use "global" as *;

.c-page-top-btn{
  position: fixed;
  bottom: rem(97);
  right: rem(10);
  z-index: 888;
  @include mq("md") {
    bottom: rem(40);
    right: rem(20);
  }
}

.c-page-top-btn img{
  width: 100%;
  max-width: rem(52);
  aspect-ratio: 1/1;
  object-fit: contain;
}